import { Component, ViewChild } from '@angular/core';
import { LoadingController,ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-transsaldo-create',
  templateUrl: 'transsaldo-create.html',
})
export class TranssaldoCreatePage {
  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  items: any=[];
  items2: any=[];
  tiposus: any=[];
  idempresa:any;
  public nombre		     : any;
  public cuitt		     : any;
  public nombre2		     : any;
  public cuitt2		     : any;
  public monto		     : any;
  public evento		     : any;
  username: string = "";
  public cui		     : any;
  public cui2		     : any;
  public idsus2		     : any;
  public idnombre2		     : any;
  public monto0		     : any;
  private _ID                  : String;
  private _IDSUSCRIPTOR                  : String;
  public image              	: any;
  public pageTitle           	: string;
  public idsuscriptor                : any;
  public idsuscriptor2                : any;
  public idcomprasaldo                : any;
  public vtarifa  :any=[];

  isDisablednit :any;
  constructor(public storage: Storage, private loadingCtrl: LoadingController,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value

             
     this.authService.getReg('TARIFA COMPRA SALDO' + '/' + this.idempresa + '/catalogo','dcatalogos').then((result) => {
      this.vtarifa=result;
    }, (err) => {  
    this.msj.showError3('Error',err.error);
  });


   
     
  })
     // Define form validation - VERY basic!
     this.form = this._FB.group({
        cui        	    : ['', Validators.required],
        cui2        	    : ['', Validators.required],
        monto     : ['', Validators.required],
     });

   

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
  this.cuitt=this.tiposus[2]
  this.cui=this.tiposus[2]
  this.idsus2=this.tiposus[3]
  this.idnombre2=this.tiposus[4]
  
          this.focused('a')

});

  }
  ionViewDidLoad() : void
  {// this._IDSUSCRIPTOR=this.navParams.data.record2.idsuscriptor;
    this.pageTitle="Trasferencia de saldo";
    if(this.navParams.get("record")==null)
    {
       
     
      
      this.isDisablednit =true
    }
    else {
      
       this._ID 				=	this.navParams.data.record._id;
       this.idsuscriptor 				=	this.navParams.data.record.idsuscriptor._id;
       this.idsuscriptor2 				=	this.navParams.data.record.idsuscriptor2._id;
       this._IDSUSCRIPTOR				=	this.navParams.data.record.idsuscriptor._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.monto			=	this.navParams.data.record.monto;
       this.isDisablednit =false
     

    }
  }

  manageGallery() : void
  {

    if(Number(this.monto)>Number(this.monto0))
    {

      this.msj.showError3('Error','Saldo insuficiente para poder transferir');
    }
    else
    {
    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }   

     let   options       : any	     = { 
       idempresa:this.idempresa,
     idsuscriptor : {id:this._IDSUSCRIPTOR,nombre:this.nombre} ,
     idsuscriptor2 : {id:this.idsuscriptor2,nombre:this.nombre2} ,
    nombre : this.nombre,
     monto : this.form.controls['monto'].value,
     url:'',
     estado : 'aprobado',
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' empresas '}
    
     }

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'compratransferencias').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'compratransferencias').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }




  focused(selectedValue: any) {
    if(( this.cui) && (this.cui!=''))
    {

     const loading = this.loadingCtrl.create({
       spinner: 'bubbles',
       content: "Espere un momento,  Buscando informaci贸n ......"
     });
     loading.present();


     this.items=[]
     this.authService.getReg(this.cui+'/'+'personasaldito/' + this.idempresa,'personals').then((data) => {
      // this.msj.showError3('Resultado','Proceso OV concluido, revisa tus datos para validar informaci贸n ');
       this.nombre=data[0].idsuscriptor.id.nombre
       this.idsuscriptor 				=	data[0].idsuscriptor.id._id;
       this._IDSUSCRIPTOR				=	data[0].idsuscriptor.id._id;
      this.monto0=data[0].saldoactual;
       this.items=data;
       loading.dismiss();
     
   }, (err) => {


 this.msj.showError3('Resultado','No CUI invalido ');
 this.cui=''
 this.items=[]
 loading.dismiss();
 

    
    
   });

    }
     
   }


   

  focused2(selectedValue: any) {
    if(( this.cui2) && (this.cui2!=''))
    {

     const loading = this.loadingCtrl.create({
       spinner: 'bubbles',
       content: "Espere un momento,  Buscando informaci贸n ......"
     });
     loading.present();


     this.items2=[]
     this.authService.getReg(this.cui2+'/'+'personasaldito/' + this.idempresa,'personals').then((data) => {
      // this.msj.showError3('Resultado','Proceso OV concluido, revisa tus datos para validar informaci贸n ');
       this.nombre2=data[0].idsuscriptor.id.nombre
       this.idsuscriptor2 				=	data[0].idsuscriptor.id._id;
       
     
       this.items2=data;
       loading.dismiss();
     
   }, (err) => {


 this.msj.showError3('Resultado','No CUI invalido ');
 this.cui2=''
 this.items2=[]
 loading.dismiss();
 

    
    
   });

    }
     
   }


}



